import { VoteCardPlugin } from '@ali/4ever-bamboo';
import { Block, Action } from '@ali/4ever-cangjie';

export const VOTING_ACTIVE = Symbol('VOTE_ACTIVE');
export const VOTE_SUBMIT = Symbol('VOTE_SUBMIT');
export const VOTE_EDITING = Symbol('VOTE_EDITING');
export const VOTE_CANCEL_EDITING = Symbol('VOTE_CANCEL_EDITING');
export const VOTE_SAVE = Symbol('VOTE_SAVE');

interface IVoteSubmit {
  userId: string;
  optionIds: string[];
  voteId: string;
  node: Block;
}

export function votingActive(node: Block): Action<{}> {
  return {
    type: VOTING_ACTIVE,
    payload: {
      node,
    },
  };
}

export function voteSubmit(payload: IVoteSubmit): Action<{}> {
  return {
    type: VOTE_SUBMIT,
    payload,
  };
}

export function voteEditing(node: Block): Action<{}> {
  return {
    type: VOTE_EDITING,
    payload: {
      node,
    },
  };
}

export function cancelEditing(node: Block): Action<{}> {
  return {
    type: VOTE_CANCEL_EDITING,
    payload: {
      node,
    },
  };
}

export function voteSave(form: VoteCardPlugin.VoteCardMetadataType): Action<{}> {
  return {
    type: VOTE_SAVE,
    payload: {
      form,
    },
  };
}
